.full-conference {
  min-height: 100vh;
  padding: 30px 20px 75px 20px;
  &__container {
    max-width: 800px;
    margin: 0 auto;
    &-top {
      display: flex;
      justify-content: space-between;
      img {
        cursor: pointer;
        -webkit-transition: all 0.2s ease-in-out;
        &:hover {
          background-color: #0000380d;
          border-radius: 5px;
        }
      }
      span {
        color: white;
        background-color: #37d175;
        height: 32px;
        padding: 6px 14px;
        border-radius: 6px;
        font-size: 14px;
      }
    }
  }
  &__title {
    padding: 16px 0;
    line-height: 31.2px;
    font-family: "Manrope semibold";
    h1 {
      font-size: 26px;
      font-weight: 700;
      @media (max-width: 1000px) {
        font-size: 26px;
      }
      @media (max-width: 768px) {
        font-size: 22px;
      }
      @media (max-width: 480px) {
        font-size: 18px;
      }
      @media (max-width: 360px) {
        font-size: 16px;
      }
    }
    small {
      color: #00003880;
      font-size: 12px;
    }
  }
  &__card {
    background-color: white;
    padding: 24px;
    border-radius: 15px;
    font-size: 14px;
    span {
      color: #00003880;
    }
    hr {
      background-color: #0000380d;
      height: 1px;
      margin: 15px 0;
    }
  }
  &__card-flex {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 3px;
    row-gap: 11px;
    @media (max-width: 480px) {
      grid-template-columns: repeat(1, 1fr);
    }
    div {
      span {
        margin-right: 3px;
      }
    }
  }

  &__tabs {
    margin: 16px 0 32px 0;
    button {
      padding: 5px 19px;
      margin-right: 16px;
      border-radius: 7px;
    }
    .button-active {
      background-color: #2c60e7;
      color: white;
      -webkit-transition: all 0.4s ease-in-out;
      &:hover {
        background-color: #4074fb;
      }
    }
    .button-passive {
      background-color: #ebefff;
      color: #2c60e7;
      -webkit-transition: all 0.4s ease-in-out;
      &:hover {
        background-color: #4074fb;
        color: white;
      }
    }
  }
  &__desc {
    @media (max-width: 480px) {
      font-size: 15px;
    }
    pre {
      white-space: pre-wrap;
      font-family: "Manrope";
      ul {
        margin-left: 20px;
        li {
          list-style-type: disc;
        }
      }

      div {
        margin-top: 20px;
        a {
          background-color: #2c60e7;
          color: white;
          border-radius: 7px;
          padding: 5px 19px;
          -webkit-transition: all 0.4s ease-in-out;
          &:hover {
            background-color: #4074fb;
          }
        }
      }
    }
    h1 {
      font-size: 22px;
      font-family: "Manrope semibold";
    }
  }
  &__contacts {
    pre {
      font-family: "Manrope";
    }
    div {
      padding-bottom: 28px;

      p {
        padding-bottom: 16px;
        line-height: 30.8px;
      }

      span {
        font-size: 22px;
        font-family: "Manrope semibold";
        padding: 0 0 16px 0;
      }
    }
    .details {
      font-family: "Manrope";
      font-size: 16px;
    }
  }
  .online,
  .offline,
  .both {
    color: black;
  }
}
